import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    //阅读历史存储
    readHistory:[],
    // 当前章节存储
    readNowData:[]
  },
  mutations: {
  },
  actions: {
   setReadHistory({state},data){
     state.readHistory.push(data);
   },
   setReadNow({state},data){
    state.readNowData=[];
     state.readNowData=data;
      
   }
  },
  modules: {
  }
})
