import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
// 引入阿里图标样式
import './assets/image/font/iconfont.css'
import './assets/image/font/iconfont.js'

import {
  NavBar,
  Form,
  Field,
  Popup,
  Icon,
  Tabbar,
  TabbarItem,
  Swipe,
  SwipeItem,
  Search,
  Toast,
  Lazyload,
  Tabs,
  Tab,
  Image,
  List,
  Cell,
  Tag,
  Overlay,
  Button,
  Uploader,
  CellGroup,
  Dialog,
  ShareSheet,
  Empty,
  Divider
} from 'vant'


import axios from 'axios'
import VueAxios from 'vue-axios'


Vue
  .use(NavBar)
  .use(Form)
  .use(Field)
  .use(Popup)
  .use(Icon)
  .use(Tabbar)
  .use(TabbarItem)
  .use(Swipe)
  .use(SwipeItem)
  .use(Search)
  .use(Toast)
  .use(Lazyload)
  .use(Tabs)
  .use(Tab)
  .use(Image)
  .use(List)
  .use(Cell)
  .use(Tag)
  .use(Overlay)
  .use(Button)
  .use(Uploader)
  .use(CellGroup)
  .use(Dialog)
  .use(ShareSheet)
  .use(Empty)
  .use(Divider)


Vue.use(VueAxios, axios)

//导入font-awesome字体图标库
import "font-awesome/css/font-awesome.min.css"

//导入用于设置rem基准模块
import 'lib-flexible';


//配置请求基础路径
axios.defaults.baseURL = 'http://www.kangliuyong.com:10002'
// 配置post请求头
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8';
//发起请求之前处理
axios.interceptors.request.use(config =>{
  //  

  if(config.method == 'post'){
    let paramsString = '';
    //post请求参数序列化，转成一个字符串
    for(let key in config.data){
      paramsString += key + '=' + config.data[key] + '&'
    }

     

    config.data = paramsString.slice(0,-1);
     
  }
  return config;
})

Vue.config.productionTip = false
//添加appkey属性保存appkey
Vue.prototype.appkey = 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=';

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
